import { Controller, Commands, Inline } from '@ali/4ever-cangjie';
import { setOwnerAndFirstCreated } from '../utils/tagStore';

export interface InsertTagdPayload {
  /**
   * 卡片子类型
   */
  tagType: string;

  /**
   * 卡片初始高度，排版需要
   */
  height: number;

  /**
   * 卡片初始宽度，排版需要
   */
  width: number;

  /**
   * 卡片元数据
   */
  metadata: any;
}

export default (controller: Controller, payload: InsertTagdPayload) => {
  const finalNode = Inline.create({
    type: 'tag',
    data: {
      ...payload,
    },
  });
  controller.command(Commands.insertInline, finalNode);

  setOwnerAndFirstCreated(finalNode.key, true);

  return controller;
};

